<script>
import rules from '@/rules'
import form from '@/mixins/form.js'
import {
  shopManageUpdate,
  validName,
  validBlNo,
  validPhone,
  validIdcard,
  validContactMail,
  shopManageGet
} from '@/api/shop/shopManage.js'
import {
  productCategoryList,
  productCategoryTree
} from '@/api/product/category.js'
import cmsMember from '@/components/cms/cmsMember.vue'
var defaultData = {
  isWechatEnable: true,
  isProductAuditEnable: true,
  auditOpinion: '',
  auditResults: 2,
  contactMail: '',
  contactName: '',
  enabled: true,
  memberId: '',
  storeExt: {
    categoryId: '',
    categoryName: '',
    guaranteedAmount: 0
  },
  member: {
    username: ''
  },
  storeInfo: {
    address: '',
    areaCode: '',
    areaName: '',
    blAreaCode: '',
    blAreaName: '',
    blCert: '',
    blCityCode: '',
    blCityName: '',
    blEndTime: '',
    blNo: '',
    blProvinceCode: '',
    blProvinceName: '',
    blStartTime: '',
    cityCode: '',
    cityName: '',
    companyName: '',
    idcardPositivePhoto: '',
    idcardReversePhoto: '',
    legalIdcard: '',
    legalName: '',
    mobile: '',
    photo: '',
    provinceCode: '',
    provinceName: '',
    registeredCapital: 0,
    scopeRange: '',
    setupDate: '',
    taxRegistrationCert: '',
    taxRegistrationCode: ''
  },
  storeName: '',
  storeSettlementAccountList: [
    {
      accountName: '',
      account: '',
      accountType: 2,
      bankExt: '',
      bankName: ''
    }
  ],
  timer: [],
  storeType: '',
  contactMobile: '',
  freePayStatus: 2,
  freePayType: 2,
  saleCateId: ''
}
export default {
  mixins: [form],
  data() {
    var ischeckRouting = (rule, value, callback) => {
      // 验证店铺名称唯一
      validName({
        validName: this.formData.storeName,
        id: this.formData.id
      }).then(res => {
        if (res.data) {
          callback()
        } else {
          callback(new Error('店铺名称已存在，不允许重复添加'))
        }
      })
    }
    var ischeckValidPhone = (rule, value, callback) => {
      // 验证联系人手机号码唯一
      validPhone({
        validPhone: this.formData.contactMobile,
        id: this.formData.id
      }).then(res => {
        if (res.data) {
          callback()
        } else {
          callback(new Error('验证联系人手机号码已存在，不允许重复添加'))
        }
      })
    }
    var validateBlNo = (rule, value, callback) => {
      // 验证营业执照注册号
      validBlNo({
        validBlNo: this.formData.storeInfo.blNo,
        id: this.formData.id
      }).then(res => {
        if (res.data) {
          callback()
        } else {
          callback(new Error('该营业执照注册号已注册店铺'))
        }
      })
    }
    var ischeckIdCard = (rule, value, callback) => {
      var patrnIdcard = /^[1-9]\d{7}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}$|^[1-9]\d{5}[1-9]\d{3}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}([0-9]|X)$/
      if (!patrnIdcard.test(value)) {
        callback(new Error('身份证格式错误'))
      } else {
        // 验证联系人身份证号唯一
        validIdcard({
          validIdcard: this.formData.storeInfo.legalIdcard,
          id: this.formData.id
        }).then(res => {
          if (res.data) {
            callback()
          } else {
            callback(new Error('验证联系人身份证号已存在，不允许重复添加'))
          }
        })
      }
    }
    var validContactMaill = (rule, value, callback) => {
      // 验证联系人邮箱唯一
      validContactMail({
        validContactMail: this.formData.contactMail,
        id: this.formData.id
      }).then(res => {
        if (res.data) {
          callback()
        } else {
          callback(new Error('联系人邮箱已存在，不允许重复添加'))
        }
      })
    }
    var validateRegMoney = (rule, value, callback) => {
      // 验证注册资本金额
      var patrnRegMoney = /^[1-9]{1}[0-9]{0,9}(.[0-9]{0,2})?$/
      if (isNaN(value)) {
        callback('金额必须为数字')
      }
      if (!patrnRegMoney.test(value)) {
        callback(new Error('正整数位最多10位，小数位最多两位'))
      } else {
        callback()
      }
    }
    // 验证联系人姓名
    var validateRegName = (rule, value, callback) => {
      var patrnName = /^[\u4E00-\u9FA5A-Za-z]+$/
      if (value === '') {
        callback(new Error('请输入联系人姓名'))
      }
      if (!patrnName.test(value)) {
        callback(new Error('用户名不规范'))
      } else {
        callback()
      }
    }
    // 验证公司电话
    var validatePhone = (rule, value, callback) => {
      var patrnPhone = /^[0][1-9]{2,3}-[0-9]{7,8}$/
      var patrnMobile = /^1(3|4|5|7|8)\d{9}$/
      if (value === '') {
        callback(new Error('请输入公司电话'))
      } else if (!patrnPhone.test(value) && !patrnMobile.test(value)) {
        callback(new Error('公司电话格式不正确'))
      } else {
        callback()
      }
    }
    return {
      formData: Object.assign({}, defaultData),
      saveRules: {
        // 修改规则
        'storeInfo.companyName': [
          rules.required('请填写公司名称'),
          rules.len(1, 25)
        ],
        'storeInfo.areaName': [rules.required('请选择公司所在地', 'change')],
        'storeInfo.address': [
          rules.required('请填写公司详细地址'),
          rules.len(1, 50)
        ],
        'storeInfo.mobile': [
          rules.required('请填写公司电话'),
          rules.custom(validatePhone)
        ],
        'storeInfo.blNo': [
          rules.required('请填写营业执照注册号'),
          rules.number(),
          rules.$length(15),
          rules.custom(validateBlNo)
        ],
        'storeInfo.registeredCapital': [
          rules.required('请填写注册成本'),
          rules.custom(validateRegMoney)
        ],
        'storeInfo.setupDate': [rules.required('请选择成立日期'), 'change'],
        'storeInfo.legalName': [
          rules.required('请填写法人姓名'),
          rules.len(1, 15),
          rules.custom(validateRegName)
        ],
        'storeInfo.legalIdcard': [
          rules.required('请填写身份证号'),
          rules.custom(ischeckIdCard)
        ],
        'storeInfo.idcardPositivePhoto': [
          rules.required('请上传身份证正面照片')
        ],
        'storeInfo.blAreaName': [
          rules.required('请选择营业执照所在地', 'change')
        ],
        'storeInfo.idcardReversePhoto': [
          rules.required('请上传身份证反面照片')
        ],
        'storeInfo.photo': [rules.required('请上传手持身份证照')],
        'storeInfo.blCert': [rules.required('请上传营业证书照')],
        'storeInfo.scopeRange': [rules.required('此项必填'), rules.len(1, 100)],
        'storeInfo.taxRegistrationCode': [
          rules.required('请填写税务登记代码'),
          rules.number(),
          rules.$length(15)
        ],
        'storeInfo.taxRegistrationCert': [rules.required('请上传税务登记证')],
        'storeInfo.blEndTime': [rules.required('请选择营业期限')],
        'storeSettlementAccountList[0].bankName': [
          rules.required('请输入开户行姓名'),
          rules.len(4, 15)
        ],
        'storeSettlementAccountList[0].accountType': [
          rules.required('此项必填')
        ],
        'storeSettlementAccountList[0].accountName': [
          rules.required('请填写姓名'),
          rules.len(1, 20)
        ],
        'storeSettlementAccountList[0].account': [
          rules.required('请填写账户'),
          rules.len(1, 25)
        ],
        timer: [rules.required('请填写选择营业期限', 'change')],

        storeName: [
          rules.required('请填写店铺名称'),
          rules.len(1, 25),
          rules.custom(ischeckRouting)
        ],
        storeType: [rules.required('请填写店铺类型')],
        contactName: [
          rules.required('请填写联系人姓名'),
          rules.len(1, 15),
          rules.custom(validateRegName)
        ],
        contactMobile: [
          rules.required('请填写联系人手机号'),
          rules.len(1, 11),
          rules.phone(),
          rules.custom(ischeckValidPhone)
        ],
        contactMail: [
          rules.required('请填写联系人邮箱'),
          rules.email(),
          rules.custom(validContactMaill),
          rules.len(6, 50)
        ],
        saleCateId: [rules.required('请选择主营类目', 'change')],
        auditResults: [rules.required('请选择审核结果')]
      },
      storeItems: [], // 店铺等级列表
      categoryInfo: [], // 分类信息
      categoryItems: [], // 商品分类列表
      dialogTableVisible: false,
      storePrices: {
        categoryName: '',
        categoryPrice: 0,
        gradeName: '',
        gradePrice: 0,
        years: 0
      },
      categoryTables: [],
      level: 0,
      regionArea: [],
      blArea: []
    }
  },
  computed: {
    totalPrice() {
      return (
        this.storePrices.categoryPrice +
        this.formData.storeExt.storeGradeLife * this.storePrices.gradePrice
      )
    }
  },
  components: {
    cmsMember
  },
  methods: {
    getStoreInfo(id) {
      this.loading = true
      shopManageGet({ id: id }).then(res => {
        this.loading = false
        this.formData = res.data
        this.$set(
          this.formData,
          'saleCateId',
          res.data.productCategory ? res.data.productCategory.id : ''
        )
        this.formData.storeInfo.setupDate = res.data.storeInfo.setupDate
          ? this.$moment(res.data.storeInfo.setupDate)
          : ''
        this.formData.timer = [
          this.$moment(res.data.storeInfo.blStartTime),
          this.$moment(res.data.storeInfo.blEndTime)
        ]
        this.regionArea = [
          res.data.storeInfo.provinceCode,
          res.data.storeInfo.cityCode,
          res.data.storeInfo.areaCode
        ]
        this.blArea = [
          res.data.storeInfo.blProvinceCode,
          res.data.storeInfo.blCityCode,
          res.data.storeInfo.blAreaCode
        ]
      })
    },
    getProductCategoryList(id, tableData) {
      // 获取商品分类下面的所有子分类
      productCategoryTree({ parentId: id }).then(res => {
        tableData.childs = res.data
        const treeData = this.treeToArray(tableData, this.categoryTables, 0)
        const arr = []
        treeData.forEach((v, i) => {
          arr[v.level] = []
          arr[v.level].push(v)
        })
      })
    },
    treeToArray(data, arrays, level) {
      for (var i = 0; i < data.length; i++) {
        var obj = data[i]
        arrays.push({
          name: obj.cateName,
          level: level
        })

        if (obj.childs != null && obj.childs.length > 0) {
          this.treeToArray(obj.childs, arrays, level + 1)
        }
      }
      return arrays
    },
    changeStoreGrade(value) {
      const grade = this.storeItems.filter(v => v.id === value)
      this.storePrices.gradeName = grade[0].levelName
      this.storePrices.gradePrice = grade[0].moeny
    },
    getCategoryInfo(item) {
      // 获取分类
      this.categoryTables = []
      this.getProductCategoryList(item.id, [
        {
          cateName: item.cateName,
          childs: []
        }
      ])
    },
    setTimer(timer) {
      // 设置时间区间
      this.formData.storeInfo.blStartTime = timer[0]
      this.formData.storeInfo.blEndTime = timer[1]
    },
    patternChange() {
      // 财务信息的结算账户类型切换方法
      this.formData.storeSettlementAccountList.account = ''
      this.formData.storeSettlementAccountList.bankExt = ''
    },
    getAreaInfo(value) {
      this.formData.storeInfo.provinceCode = value[0].code
      this.formData.storeInfo.provinceName = value[0].areaName
      this.formData.storeInfo.cityCode = value[1].code
      this.formData.storeInfo.cityName = value[1].areaName
      this.formData.storeInfo.areaCode = value[2].code
      this.formData.storeInfo.areaName = value[2].areaName
    },
    getBlAara(value) {
      this.formData.storeInfo.blProvinceCode = value[0].code
      this.formData.storeInfo.blProvinceName = value[0].areaName
      this.formData.storeInfo.blCityCode = value[1].code
      this.formData.storeInfo.blCityName = value[1].areaName
      this.formData.storeInfo.blAreaCode = value[2].code
      this.formData.storeInfo.blAreaName = value[2].areaName
    },
    save(state) {
      this.$confirm(
        '审核通过后，系统会根据缴费金额是否大于0，选择不同的处理模式。金额等于0时，会自动完成审核并成功开通；反之，只会审核当前信息，并需要申请者成功支付费用后才能完成店铺的正式开通，请确认是否继续',
        '提示',
        {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }
      ).then(() => {
        const params = Object.assign({}, this.formData)
        delete params.member
        // delete params.timer
        this.saveForm(
          shopManageUpdate,
          params,
          state,
          '/shopManage/enterApplication',
          'list'
        )
      })
      // 添加
    },
    getMenuTree() {
      // 获取分类节点
      productCategoryList().then(res => {
        this.categoryItems = res.data
      })
    }
  },
  created() {
    this.formData.storeType = this.$route.query.type
    this.getStoreInfo(this.$route.query.id)
    this.getMenuTree()
  }
}
</script>
<style>
.costTable {
  border: 0px solid #b9b9b9;
  text-align: center;
}
.costTable tr {
  padding: 0;
  border: 0px;
}
.costTable tr td {
  border: 1px solid #b9b9b9;
  border-top: 0px;
  margin: 0;
  padding: 0;
  width: 80px;
}
.costTable tr th {
  border: 1px solid #b9b9b9;
  margin: 0;
  padding: 0;
}
.costP {
  text-align: center;
  margin: 0;
  padding: 0;
}
.costSpan {
  color: #999999;
}
.costPTimes {
  position: absolute;
  right: 10px;
  top: 0px;
}
.el-input__icon {
  line-height: 14px !important;
}
</style>



